export const fileFormat = (extension) => {
  if (!extension) return null
  switch (extension.toLowerCase()) {
    case 'csv':
      return 'text/csv'
    case 'xls':
      return 'application/vnd.ms-excel'
    case 'xlsx':
      return 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
    default:
      return null
  }
}